@charset "UTF-8";
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 200;
  src: url("../fonts/Roboto-Thin.ttf");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Roboto-Light.ttf");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.ttf");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Roboto-Medium.ttf");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Roboto-Bold.ttf");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/Roboto-Black.ttf");
  font-display: swap;
}
:root {
  --font: "Roboto", "Arial", sans-serif;
  --white: #fff;
  --black: #000;
  --base-black: #333;
  --grey: #ccc;
  --simple-grey: #f1f1f1;
  --red: #dc3545;
  --green: #3ac47d;
  --blue: #0069d9;
}

body {
  color: var(--base-black);
  font-family: var(--font);
  margin: 0;
  min-width: 320px;
}

.fa-trash,
.fa-check {
  pointer-events: none;
}

.todo {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  width: 100%;
  max-width: 500px;
  min-height: 90vh;
  margin: 5vh auto;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
}

.todo__title {
  font-size: 80px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 40px;
  opacity: 0.5;
}

.todo__wrap {
  display: flex;
  flex-direction: column;
}

.todo__form {
  margin-bottom: 10px;
  position: relative;
}

.todo__input {
  font-family: var(--font);
  font-size: 14px;
  width: 100%;
  padding: 10px 40px 10px 20px;
  border: 1px solid var(--blue);
  box-sizing: border-box;
}

.todo__form-submit {
  width: 40px;
  height: 100%;
  color: var(--blue);
  background: none;
  border: 1px solid var(--blue);
  cursor: pointer;
  transition: 0.2s;
  position: absolute;
  top: 0;
  right: 0;
}
.todo__form-submit:hover, .todo__form-submit:focus {
  color: var(--white);
  background-color: var(--blue);
}

.todo__list {
  list-style: none;
  margin-top: 30px;
  margin-bottom: 0;
  padding-left: 0;
}

.todo__item {
  display: flex;
  align-items: center;
  padding-right: 10px;
  background-color: var(--simple-grey);
  border-radius: 5px;
  margin-bottom: 5px;
  transition: 0.2s;
}
.todo__item:last-child {
  margin-bottom: 0;
}
.todo__item--checked {
  text-decoration: line-through;
  text-decoration-color: var(--red);
}

.todo__text {
  flex: 1;
  font-size: 16px;
  text-align: left;
  padding: 10px 20px;
}

.todo__button {
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  background: none;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.2s;
}
.todo__button--check {
  color: var(--green);
  margin-right: 5px;
}
.todo__button--check:hover, .todo__button--check:focus-visible {
  color: var(--white);
  background-color: var(--green);
}
.todo__button--delete {
  color: var(--red);
}
.todo__button--delete:hover, .todo__button--delete:focus {
  color: var(--white);
  background-color: var(--red);
}

.select {
  position: relative;
}
.select::after {
  display: flex;
  justify-content: center;
  align-items: center;
  content: "â–¼";
  color: var(--white);
  width: 30px;
  height: 100%;
  background: var(--black);
  opacity: 0.8;
  cursor: pointer;
  pointer-events: none;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}

.select__filters {
  background: none;
  appearance: none;
  color: var(--base-black);
  font-family: var(--font);
  font-size: 14px;
  width: 100%;
  height: 100%;
  padding: 5px 30px 5px 20px;
  border: 1px solid var(--black);
  cursor: pointer;
}